<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <b>学科：</b>
<el-input
 v-model="input"
  placeholder="请输入学科"
  style="width: 150px;"
  ></el-input>
<el-button type="primary" @click="searchFun" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" 
        @click="reset"
        icon="el-icon-magic-stick">重置</el-button>

      </el-row>



 <template>
  <el-table
    :data="tableData"
    border
    style="width: 100%"
    max-height="500"
    @selection-change="handleSelectionChange"
    >
     <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
        prop="id"
        label="ID"
        >
      </el-table-column>
      <el-table-column
        prop="name"
        label="学科">
      </el-table-column>
      <el-table-column
        prop="score"
        label="分数">
      </el-table-column>
  </el-table>

  <div class="block">
    <el-pagination
     @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="pagesizes"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>
</el-card>

    
  </div>
</template>

<script>
import API from "../../utils/service"
import axios from 'axios'
export default {
   data() {
        return {
          name: '我的成绩',
          tableData: [],
          input: '',
          currentPage: 1,
          total: 0,
          pagesize: 5,
          pagesizes: [3,4,5,6],
          ids: [],
          score: {
            id: 0,
            name: '心理学',
            score: '90'
          },
          multipleSelection: []
        }
      },
  mounted(){
    this.$bus.$emit('receiveParams',this.name)
    this.init()
    this.pages(1,this.pagesize)
  },
  methods: {
      handleCurrentChange(val) {
        console.log(val);
        this.pages(val,this.pagesize)
      },
       handleSizeChange(val) {
        this.pagesize = val
        this.pages(this.currentPage,this.pagesize)
      },
    init(){
      API({
        url:'/scoreController/getScore',
        method: 'get',
      }).then((res)=>{
        this.total = res.data.data.length
      })
    },
    pages(val,page) {
      // console.log('查询页码:',val,'页数:',page);
      API({
        url:`/scoreController/getScore/${val}/${page}`,
        method: 'get',
      }).then((response)=>{
        this.tableData = response.data.data.records
        })
    },
    searchFun() {
      API({
        url:`/scoreController/getScore/1/${this.total}`,
        params: {
          scoreName: this.input
        },
        method: 'get',
      }).then((res)=>{
        this.input = ''
        this.tableData = res.data.data.records
      })

    },
    reset() {
      this.init()
      this.pages(1,this.pagesize)
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
        this.ids = []
        this.multipleSelection.map(item=>{
          console.log(item.id);
          this.ids.push(item.id)
        })
    },
  }
}
</script>

<style lang="scss" scoped>
  .el-row{
    margin: 30px 0 50px 0;
  }
  .text {
    font-size: 20px;
  }

  // .item {
  //   padding: 18px 0;
  // }

  .box-card {
    width: 100%;
  }

  .el-card{
    padding: 0px;
  }

  .el-button--primary {
    background-color: #FAB6B6;
    border-color: #FAB6B6;
  }

  .el-button--primary:focus, .el-button--primary:hover {
    background: #e9baba;
    border-color: #f3dfdf;
    color: #FFF;
}

  ::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
	background-color: #f3dfdf;
}



</style>